import React from 'react';
import styles from './Job.module.css';

const Job = props => {
  return (
    <div className={styles.Job}>
      <div className={styles.Info}>
        <div className={styles.JobName}>{props.data.name}</div>
        <div className={styles.JobType}>{props.data.jobType}</div>
      </div>
      <div className={styles.SecondInfo}>
        <div className={styles.Row}>
          <div className={styles.InfoTitle}>TYPE OF SHIFTS</div>
          <div className={styles.ShiftType}>{props.data.shiftType}</div>
        </div>
        <div className={styles.Row}>
          <div className={styles.InfoTitle}>BEGIN</div>
          <div className={styles.BeginDate}>{props.data.beginDate}</div>
        </div>
      </div>
      <div className={styles.Actions}>
        <div className={styles.Id}>ID {props.data.id}</div>
        <div className={styles.Buttons}>
          <div className={styles.DetailsButton}>Details</div>
          <div
            className={styles.ApplyButton}
            onClick={() => {
              props.click(props.data);
            }}
          >
            Apply
          </div>
        </div>
      </div>
    </div>
  );
};

export default Job;
